#root, .App {
    height: 100%;
  }
  
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
    height: 80px;
  }
  
  .App-header {
    background-color: #222;
    height: 150px;
    padding: 20px;
    color: white;
  }
  
  .App-title {
    font-size: 1.5em;
  }
  
  .app-intro {
    padding-top: 50px;
  }
  
  .app-content {
    margin: 0 auto;
    max-width: 800px;
    height: 100%;
  }
  
  @media screen and (max-width: 1065px) {
    .app-content {
      margin: 0 20px;
    }
  }
  
  @keyframes App-logo-spin {
    from { transform: rotate(0deg); }
    to { transform: rotate(360deg); }
  }